import { PLACEHOLDER_REMOVE } from '../actions';
import removePlaceholder from '../commands/removePlaceholder';

export default function onAction(action, editor, next) {
  const { type, payload } = action;
  if (type === PLACEHOLDER_REMOVE) {
    const { node, data } = payload;
    // @ts-ignore 函数只接收两个参数
    return removePlaceholder(editor, node, data);
  }

  return next();
}
